.profile__name{
    color: white;
    margin: 0;
    font-weight: 500;
    font-size: 42px;
    line-height: 48px;
    max-width: 294px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}
@media screen and (max-width: 320px){
    .profile__name{
        font-size: 27px;
        line-height: 33px;
        text-align: center;
        max-width: 189px;
    }
}
