.header{
    max-width: 880px;
    border-bottom: 1px solid rgb(54,54,54);
    margin: 45px auto 0;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}
@media screen and (max-width: 320px){
    .header{
        width: 100%;
        margin-top: 28px;
    }
}